<template>
<div :style="`margin-bottom: ${sticky ? (this.bgHeight === 0 ? 112 : 56) : 0}px;`">
  <div ref="hbg"><slot></slot></div>
  <v-toolbar elevated :style="`z-index: 10; top: ${-toolbarHeight}px; padding-left: 5px; width: 100%; background: ${bgColor}`" ref="toolbar" :class="sticky ? 'stickytop' : ''">
    <v-avatar><img src="https://metanoia-rehab.ru/trezvey-content/themes/lechenie/img/logo.png"/></v-avatar>
    <v-toolbar-title :style="`opacity: ${progress * 2 - 1}; width: ${100 / (buttons.length + progress) * progress}%`"><v-btn style="color: white" text to="/">Метанойя</v-btn></v-toolbar-title>
    <v-btn text tile v-for="btn in buttons" :key="btn.label" v-text="btn.label" :to="btn.to" :style="`color: white; height: 100%; width: ${100 / (buttons.length + progress)}%`"/>
  </v-toolbar>
</div>
</template>

<style scoped>
.stickytop {
  position: fixed;
  top: 0px !important;
}
</style>

<script>
export default {
  data: () => ({
    buttons: [
      { label: 'О нас' },
      { label: 'Лечение зависимостей', to: 'types' },
      { label: 'Цены', to: 'price' },
      { label: 'Контакты', to: 'contacts' },
    ],
    bgHeight: 0,
    toolbarHeight: 0,
    progress: 1,
    sticky: true,
    bgColor: 'rgba(0,0,0,0)'
  }),

  mounted() {
    window.addEventListener('resize', this.recalculateScrollPos);
    window.addEventListener('scroll', this.onWindowScroll);
    this.recalculateScrollPos();
    this.onWindowScroll();
  },

  unmounted() {
    window.removeEventListener('resize', this.recalculateScrollPos);
    window.removeEventListener('scroll', this.onWindowScroll);
  },

  methods: {
    recalculateScrollPos() {
      this.bgHeight = this.$refs.hbg.clientHeight;
      this.toolbarHeight = this.$refs.toolbar.$el.clientHeight;
      this.progress = this.bgHeight === 0 ? 1 : Math.min(1, Math.max(0, window.pageYOffset / (this.bgHeight - this.toolbarHeight)));
      this.recalculateBgColor();
    },

    onWindowScroll() {
      this.sticky = this.bgHeight === 0 ? true : window.pageYOffset >= this.bgHeight - this.toolbarHeight;
      this.progress = this.bgHeight === 0 ? 1 : Math.min(1, Math.max(0, window.pageYOffset / (this.bgHeight - this.toolbarHeight)));
      this.recalculateBgColor();
    },

    recalculateBgColor() {
      const lerp = (start, end) => (1 - this.progress) * start + this.progress * end;
      const fc = [0,0,0,0.2];
      const tc = [52,144,220,1];

      if (this.sticky) {
        this.bgColor = `rgba(${tc[0]}, ${tc[1]}, ${tc[2]}, ${tc[3]})`;
        return;
      }

      this.bgColor = `rgba(${lerp(fc[0], tc[0])}, ${lerp(fc[1], tc[1])}, ${lerp(fc[2], tc[2])}, ${lerp(fc[3], tc[3])})`;
    }
  }
}
</script>
