<template>
<v-container>
<v-row>
  <v-col md="4" v-for="(item, index) in usedServices" :key="item.label">
    <v-card dense class="mx-auto" max-width="344" outlined :to="`/types/${item.type}`" :color="colors[index]">
      <v-list-item three-line>
        <v-list-item-content>
          <v-list-item-title class="headline mb-1" style="white-space: pre-wrap;">
            {{item.label}}
          </v-list-item-title>
        </v-list-item-content>

        <v-list-item-avatar
          size="80"
          icon
        ><v-icon x-large>{{item.icon}}</v-icon></v-list-item-avatar>
      </v-list-item>
    </v-card>
  </v-col>
</v-row>
</v-container>
</template>

<script>
import services from '@/services.js';

export default {
  data: () => ({
    services,
    colors: [
      'primary',
      'pink',
      'green'
    ],
    usedServices: [
      'Лечение наркомании',
      'Лечение алкоголизма',
      'Лечение игромании'
    ].map(v => services.find(b => b.label === v))
  })
}
</script>